import React, { Component } from "react";

const Homepage = () => {
    return (
      <div className="container-fluid">
      </div>
    )
}


class LeftSideBar extends Component {
  state = {
    search: '',
  }
  createSearch = (e) => {
    return this.setState({search: e.target.value})
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="left-side-bar col-bg-2">
          <div className="left-side-body">
            <form id="left-bar-form" onSubmit={ (e) => { e.preventDefault(); alert(this.state.search)}}> 
              <label htmlFor='search'></label>
              <input type="text" value={this.state.search} onChange={this.createSearch}/>
              <button value="Submit">Search Foods</button>
            </form>
          </div>
        </div>
  
      </div>
    )
  }
}

export { 
  Homepage, 
  LeftSideBar 
}